<template>
  <v-card class="rounded-lg">
    <v-card-title>
      Usuários

      <v-spacer></v-spacer>

      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="usuarios"
      :search="search"
      :footer-props="{ 'items-per-page-options': [50, 100, -1] }"
      sort-by="titulo"
      class="elevation-1 rounded-lg"
    >
      <template v-slot:[`item.is_staff`]="{ item }">
        <v-icon v-if="item.is_staff">mdi-check</v-icon>
      </template>

      <template v-slot:[`item.is_superuser`]="{ item }">
        <v-icon v-if="item.is_superuser">mdi-check</v-icon>
      </template>

      <template v-slot:[`item.is_active`]="{ item }">
        <v-icon v-if="item.is_active">mdi-check</v-icon>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <RemoverUsuario :id="item.id" @form-submit="remover_usuario" />
        <AtualizarUsuario :usuario="item" />
      </template>

      <template v-slot:no-data>Nenhum Usuário Cadastrado!</template>
    </v-data-table>
  </v-card>
</template>

<script>
import RemoverUsuario from "./components/RemoverUsuario";
import AtualizarUsuario from "./components/AtualizarUsuario";
import api from "@/services/api";
import verifica_token_storage from "@/utils/verifica_token_storage";
import { mapMutations } from "vuex";

export default {
  name: "AdminUsuariosView",

  components: {
    RemoverUsuario,
    AtualizarUsuario,
  },

  data: () => ({
    search: "",
    headers: [
      { text: "Código", align: "center", value: "id" },
      { text: "Usuário", align: "start", value: "username" },
      { text: "E-mail", align: "start", value: "email" },
      { text: "Desenvolvedor?", align: "start", value: "is_staff" },
      { text: "Admin?", align: "start", value: "is_superuser" },
      { text: "Ativo?", align: "start", value: "is_active" },
      { text: "Data da Criação", align: "start", value: "date_joined" },
      { text: "Data do Ultimo Login", align: "start", value: "last_login" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    usuarios: [],
  }),

  created() {
    this.get_usuarios();
  },

  methods: {
    ...mapMutations(["CarregandoMutation"]),

    get_usuarios() {
      this.CarregandoMutation();

      const headers = verifica_token_storage();

      api
        .get("admin/usuarios", { headers })
        .then((response) => {
          this.usuarios = response.data.data;
        })
        .catch((error) => {
          console.log(error);
        })
        .finally(() => {
          this.CarregandoMutation();
        })
    },

    remover_usuario(usuario_id) {
      this.usuarios = this.usuarios.filter((item) => item.id != usuario_id);
    },
  },
};
</script>
